@import './AddPartMenuListItem/AddPartMenuListItem.scss';
@import './AddPartMenuResultsSummary/AddPartMenuResultsSummary.scss';

.add-part-menu {

	--max-height: 65vh;

	max-width: 80vw;
	min-width: 65vw;

	--heading-height: 45px;

	display: flex;

	header {
		margin-bottom: 10px;
	}

	.content-wrapper {
		display: flex;
		flex-direction: row;
		height: var(--max-height);

		.loader {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 100%;
			height: 100%;
		}
		
		> aside {
			border: 1px solid #ddd;
			margin-right: 10px;
			flex: 1;
			width: 25%;
			

			.filter-header {
				line-height: 0;
				padding: 10px;
				border-bottom: 1px solid #ddd;
				display: flex;
				flex-direction: row;
				align-items: center;
				height: 45px;
				gap: 10px;

				a {
					line-height: 0;
				}
			}

			.tree-container {
				background: white;
				// margin-right: 5px;
				// width: 25%;
				
				overflow: auto;
				max-height: calc(100% - 45px);		
			}
		}

		main {
			flex: 3;
			
			border: 1px solid #ddd;

			.results {
				border-top: 1px solid #ddd;
				overflow: auto;
				max-height: calc(100% - 45px);			 	
			}

			.no-results {
				padding: 10px;
				text-align: center;
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;
				gap: 20px;
				// width: 50%;
				// margin: 0 auto;
				height: 100%;

				.add-part-menu-results-summary {
					justify-content: center;
					gap: 7px;
				}


				.callout {
					max-width: 300px;
				}
			}
		}
	}

}